.container {
    width: 100px;
    height: 100%;
    background-color: #d9d9d9;
    border: black;
    border-style: solid;
    border-width: 0.5px;
    align-items: center;
    justify-content: space-between;
    display: flex;
    padding-left: 34px;
  }
 
  .container:hover {
    background-color: #ffa800;
    transition: 0.5s;
  }